import React, { useState, useEffect, useRef } from 'react'
import { v4 as uuid } from 'uuid'

import config from '../../../config/config'
import LayoutsConfig from './Layouts.config'

import i18n from '../../../controller/i18n'
import Log from '../../../controller/Log'
import { moveKeyInArray } from '../../../helpers/utils'

import Grid from '@material-ui/core/Grid'
import IconButton from '@material-ui/core/IconButton'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'

import AddIcon from '@material-ui/icons/AddCircle'
import TabIcon from '@material-ui/icons/Tab'
import ArrowUpIcon from '@material-ui/icons/KeyboardArrowUp'
import ArrowDownIcon from '@material-ui/icons/KeyboardArrowDown'
import ArrowLeftIcon from '@material-ui/icons/KeyboardArrowLeft'
import ArrowRightIcon from '@material-ui/icons/KeyboardArrowRight'
import FullscreenIcon from '@material-ui/icons/Fullscreen'
import DeleteIcon from '@material-ui/icons/Delete'

import Inputs from '../../../components/Inputs'
import WidgetPage from './WidgetPage'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	deleteButton: {
		marginTop: '20px'
	},
	tabs: {
		marginBottom: '10px'
	},
	tab: {
		padding: theme.spacing(3, 2),
	},
	iconContainer: {
		flexDirection: 'column',
		alignItems: 'center'
	},
	columnContainer: {
		marginTop: theme.spacing(2),
	},
	column: {
		width: 250,
		margin: theme.spacing(1, 1),
		borderRight: '1px solid rgba(0, 0, 0, 0.12)',
		borderLeft: '1px solid rgba(0, 0, 0, 0.12)',
	},
	DialogActions: {
		justifyContent: 'initial'
	},
	alignRight: {
		display: 'flex',
		justifyContent: 'flex-end',
		
		'& > :not(:first-child)': {
			marginLeft: 8,
		}
	}
}));


i18n.setTranslations(language => require('./i18n/' + language));

const LayoutsPage = React.memo(props => {
	Log._('LayoutsPage', 'RENDER LayoutsPage', 'development');
	
	const layout = useRef(Array.isArray(props.layout) && props.layout.length > 0 ? [ ...props.layout ] : [{ title: '', tabs: [] }]);
	
	/**
	 *
	 */
	const _setLayout = (setter, cb = null) => {
		layout.current = setter(layout.current);
		cb && cb();
		
		props._onLayoutsChange(layout.current);
	}
	
	/*
	 *
	 */
	const drawers = layout.current.map((drawer, drawerIndex) => {
		
		if (!drawer) {
			return null;
		}
		
		drawer.id = drawer.title.toLowerCase().replace(/ /g, '') + drawerIndex;
		return <Drawer
					{...props}
					key={'Drawer-' + drawer.id} 
					_setLayout={_setLayout}
					layout={layout.current}
					drawer={{ ...drawer, index: drawerIndex, id: drawer.id }}
					/>
	});
		
	return (
		<Paper>
			{drawers}
		</Paper>
	)
	
}, (prevProps, nextProps) => nextProps.selected !== true); // re-render on false (not equal), only render when selected

export default LayoutsPage;


/*
 * DRAWER
 */
const Drawer = React.memo(props => {
	Log._('LayoutsPage', 'RENDER Drawer ' + props.drawer.id, 'development');
	const classes = useStyles();
	const { _setLayout } = props;
	
	const [drawer,setDrawer] = useState(props.drawer || {});
	
	/*
	 * apply changes to layout
	 */
	const isFirstRun = useRef(true);
	useEffect(() => {
		isFirstRun.current === false && _setLayout(prevLayout => {
			let nextLayout = [ ...prevLayout || [] ];
			nextLayout[drawer.index] = drawer;
			return nextLayout;
		});
		
		isFirstRun.current = false;
		
		// new tab
		const newTab = document.getElementById('Drawer-' + drawer.id + '-Tab-' + i18n.t('add tab').toLowerCase().replace(/ /g, '') + (drawer.tabs.length-1));
		
		if (newTab) {
			document.getElementById('DialogContent').scrollTop = newTab.offsetTop;
		}
		
	}, [_setLayout, drawer]);
	
	/**
	 *
	 */
	const _addTab = d => {
		
		setDrawer(prevDrawer => {
			let nextDrawer = { ...prevDrawer };
			nextDrawer.tabs.push({
				title: i18n.t('add tab'),
				icon: 'home',
				columns: [[]]
			});
			
			return nextDrawer;
		});
	}
	
	/**
	 *
	 */
	const _deleteTab = tabIndex => {
		
		setDrawer(prevDrawer => {
			let nextDrawer = { ...prevDrawer };
			nextDrawer.tabs.splice(tabIndex, 1);
			return nextDrawer;
		});
	}
	
	/**
	 *
	 */
	const _moveTab = (action, tabIndex) => event => {
		event.stopPropagation();
		
		setDrawer(prevDrawer => {
			let nextDrawer = { ...prevDrawer };
			nextDrawer.tabs = moveKeyInArray([ ...nextDrawer.tabs || [] ], tabIndex, action);
			return nextDrawer;
		});
	}
	
	// tabs per drawer
	const tabs = drawer.tabs.map((tab, tabIndex) => {
		
		if (!tab) {
			return null;
		}
		
		tab.id = uuid();
		return <Tab
				{...props}
				key={'Tab-' + tab.id} 
				tab={{ ...tab, index: tabIndex, id: tab.title.toLowerCase().replace(/ /g, '') + tabIndex }}
				_moveTab={_moveTab}
				_deleteTab={_deleteTab}
				/>
	});
	
	return (
		<List className={classes.tabs} key={'Drawer-' + drawer.id} id={'Drawer-' + drawer.id}>
			<ListItem divider classes={{ root: classes.tab }}>
				<ListItemIcon classes={{ root: classes.iconContainer }}>
					<TabIcon />
				</ListItemIcon>
				<ListItemText
					disableTypography={true}
					primary={[
						<Inputs.Button key='Input-addTabs' id='Input-addTabs' label='add tab' onClick={() => _addTab(drawer)} />,
						<Inputs.Switch
							key='Input-tabsHideLabels'
							id='tabsHideLabels'
							{...LayoutsConfig.tabsHideLabels}
							
							onChange={({ id }, val) => props._onLayoutsChange(null, { [id]: val })}
							val={props.settings['tabsHideLabels']}
							/>
					]}
					/>
				
			</ListItem>
			{tabs}
			
		</List>
	)

});

/*
 *
 */
const Tab = React.memo(props => {
	Log._('LayoutsPage', 'RENDER Tab ' + props.tab.id, 'development');
	const classes = useStyles();
	const { _setLayout, drawer } = props;
	
	const [tab,setTab] = useState(props.tab || {});
	
	/*
	 * apply changes to layout
	 */
	const isFirstRun = useRef(true);
	useEffect(() => {
		isFirstRun.current === false && _setLayout(prevLayout => {
			let nextLayout = [ ...prevLayout || [] ];
			nextLayout[drawer.index].tabs[tab.index] = tab;
			return nextLayout;
		});
		
		isFirstRun.current = false;
	}, [_setLayout, drawer.index, tab]);
	
	/**
	 *
	 */
	const _changeTab = (drawerIndex, tabIndex) => ({ id }, val) => {
		id = id.substr(0, id.indexOf('-'));
		
		// edit tab icon / label
		if ((id === 'icon' || id === 'title') && tab[id] !== val) {
			
			setTab(prevTab => ({
				...prevTab,
				[id]: val
			}));
		}
		
		// edit tab fullscreen option
		else if (id === 'fullscreen') {
			
			setTab(prevTab => {
				let nextTab = { ...prevTab }
				
				if (nextTab._fullscreen !== undefined) {
					nextTab.columns = [
						[ nextTab._fullscreen ]
					];
					
					delete nextTab._fullscreen;
				}
				else {
					nextTab._fullscreen = (nextTab.columns && nextTab.columns[0] && nextTab.columns[0][0]) || null;
					delete nextTab.columns;
				}
				
				return nextTab;
			});
		}
	}
	
	/**
	 *
	 */
	const _addColumn = () => {
		
		setTab(prevTab => {
			let nextTab = { ...prevTab || {} }
			nextTab.columns.push([]);
			return nextTab;
		});
	}
	
	/**
	 * 
	 */
	const _deleteColumn = columnIndex => {
		
		setTab(prevTab => {
			let nextTab = { ...prevTab || {} }
			nextTab.columns.splice(columnIndex, 1);
			return nextTab;
		});
	}
	
	/**
	 * 
	 */
	const _moveColumn = (action, columnIndex) => {
		
		setTab(prevTab => {
			let nextTab = { ...prevTab || {} }
			nextTab.columns = moveKeyInArray([ ...nextTab.columns || [] ], columnIndex, action);
			return nextTab;
		});
	}
	
	// columns per tab
	let columns = 1;
	let widgetsInFirstColumn = (tab.columns && tab.columns[1] && tab.columns[1].length) || 0;
	let tabColumnsNodes = [];
	
	// tab is fullscreen
	if (tab._fullscreen !== undefined) {
		const column = {
			id: uuid(),
			index: 'fullscreen',
			widgets: tab._fullscreen ? [ tab._fullscreen ] : []
		}
		
		tabColumnsNodes.push(
			<Column
				{...props}
				key={'Column-' + column.id}
				column={column}
				tab={tab}
				/>
		);
	}
	
	// tab divided in columns
	else {
		columns = tab.columns.length;
		tabColumnsNodes = tab.columns.map((widgets, columnIndex) => {
			const column = {
				id: uuid(),
				index: columnIndex,
				widgets
			}
			
			return <Column
						{...props}
						key={'Column-' + column.id}
						_moveColumn={_moveColumn}
						_deleteColumn={_deleteColumn}
						column={column}
						tab={tab}
						/>
					
		});
	}
	
	return (
		<ListItem
			id={'Drawer-' + drawer.id + '-Tab-' + tab.id}
			divider={tab.index !== (props.layout[drawer.index].tabs.length-1)}
			classes={{ root: classes.tab }}
			>
			
			<ListItemIcon classes={{ root: classes.iconContainer }}>
				<React.Fragment>
					<IconButton size="small" onClick={props._moveTab('up', tab.index)}>
						<ArrowUpIcon />
					</IconButton>
					<IconButton size="small" onClick={props._moveTab('down', tab.index)}>
						<ArrowDownIcon />
					</IconButton>
					<IconButton size="small" onClick={() => props._deleteTab(tab.index)} classes={{ root: classes.deleteButton }}>
						<DeleteIcon />
					</IconButton>
				</React.Fragment>
			</ListItemIcon>
			<ListItemText
				disableTypography={true}
				primary={[
					<Inputs.Text
						key={'Input-tabIcon-' + tab.id}
						id={'icon-' + tab.index}
						{...LayoutsConfig.tabIcon}
						
						onBlur={_changeTab(drawer.index, tab.index)}
						val={tab.icon || ''}
						icon={tab.icon ? tab.icon.replace('mdi-', '') : ''}
						/>,
					
					<Inputs.Text
						key={'Input-tabTitle-' + tab.id}
						id={'title-' + tab.index}
						{...LayoutsConfig.tabTitle}
						
						onBlur={_changeTab(drawer.index, tab.index)}
						val={tab.title || ''}
						/>,
					
					<Inputs.Button
						key={'Input-tabAddColumn-' + tab.id}
						id={'tabAddColumn-' + tab.index}
						label='add column'
						disabled={tab._fullscreen !== undefined}
						onClick={_addColumn}
						/>,
					
					<Inputs.Switch
						key={'Input-tabFullscreen-' + tab.id}
						id={'fullscreen-' + tab.index}
						{...LayoutsConfig.tabFullscreen}
						
						disabled={(columns > 1 || widgetsInFirstColumn > 1)}
						onChange={_changeTab(drawer.index, tab.index)}
						val={tab._fullscreen !== undefined}
						/>,
					
					<Grid
						key={'Tab-' + tab.id + '-ColumnContainer'}
						classes={{ root: classes.columnContainer }}
						container
						>
						
						{tabColumnsNodes}
					</Grid>
				]}
				/>
			
		</ListItem>
	);

});

/*
 *
 */
const Column = React.memo(props => {
	Log._('LayoutsPage', 'RENDER Column ' + props.column.index, 'development');
	const classes = useStyles();
	const { _setLayout, drawer, tab } = props;
	
	const [column,setColumn] = useState(props.column);
	const [selectedWidget,setSelectedWidget] = useState(null);
	
	/*
	 * apply changes to layout
	 */
	const isFirstRun = useRef(true);
	useEffect(() => {
		isFirstRun.current === false && _setLayout(prevLayout => {
			let nextLayout = [ ...prevLayout || [] ];
			
			// fullscreen
			if (column.index === 'fullscreen') {
				nextLayout[drawer.index].tabs[tab.index]._fullscreen = column.widgets[0];
				delete nextLayout[drawer.index].tabs[tab.index].columns;
			}
			
			// column
			else {
				nextLayout[drawer.index].tabs[tab.index].columns[column.index] = column.widgets;
			}
			
			return nextLayout;
		});
		
		isFirstRun.current = false;
	}, [_setLayout, drawer.index, tab.index, column]);
	
	/**
	 *
	 */
	const _openWidget = widget => setSelectedWidget(widget);
	
	/**
	 * 
	 */
	const _saveWidget = widget => {
		const widgetIndex = widget.index;
		delete widget.index;
		
		// set default states
		if (widget.devices) {
			widget.devices = widget.devices.map(device => {
				const states = props.devices[device.deviceId] && props.devices[device.deviceId].states && Object.keys(props.devices[device.deviceId].states);
				
				// set primaryStateKey
				if (states && !device.primaryStateKey) {
					device.primaryStateKey = states[0];
				}
				
				// set bodyStateKey
				if (states && !device.bodyStateKey && device.bodyElement) {
					device.bodyStateKey = states[0];
				}
				
				return device;
			});
		}
		
		// save widget
		setSelectedWidget(null);
		setColumn(prevColumn => {
			let nextColumn = { ...prevColumn || {} }
			
			// fullscreen
			if (column.index === 'fullscreen') {
				nextColumn.widgets[0] = widget;
			}
			
			// column
			else {
				// modify existing
				if (widgetIndex >= 0) {
					nextColumn.widgets[widgetIndex] = widget;
				}
				// add new
				else {
					nextColumn.widgets.push(widget);
				}
			}
			
			return nextColumn;
		});
	}
	
	/**
	 * 
	 */
	const _deleteWidget = widgetIndex => {
		setSelectedWidget(null);
		setColumn(prevColumn => {
			let nextColumn = { ...prevColumn || {} }
			nextColumn.widgets.splice(widgetIndex, 1);
			return nextColumn;
		});
	}
	
	/**
	 * 
	 */
	const _moveWidget = (action, widgetKey) => event => {
		event.stopPropagation();
		
		setColumn(prevColumn => {
			let nextColumn = { ...prevColumn || {} }
			nextColumn.widgets = moveKeyInArray([ ...nextColumn.widgets || [] ], widgetKey, action);
			return nextColumn;
		});
	}
	
	// components per tab
	let tabColumnWidgetsNodes = [];
	tabColumnWidgetsNodes = column.widgets.map((widget, index) => {
		
		if (widget !== null) {
			widget.id = uuid();
			widget.index = index;
			widget.module = widget.module || config.get('DEFAULT_MODULE');
			
			return <Widget
					{...props}
					key={'Widget-' + widget.id}
					widget={widget}
					_moveWidget={_moveWidget}
					_openWidget={_openWidget}
					/>
		}
		
		return null;
	});
	
	return (
		<>
			{selectedWidget !== null && <WidgetPage
											devices={props.devices}
											widget={selectedWidget}
											onSave={_saveWidget}
											onDelete={_deleteWidget}
											onClose={() => setSelectedWidget(null)}
											/>}
			
			<Grid className={classes.column} item>
				<List>
					{tab._fullscreen === undefined && <>
					<ListItem divider dense>
						<ListItemIcon classes={{ root: classes.iconContainer }}>
							<IconButton size="small" onClick={() => props._moveColumn('up', column.index)}>
								<ArrowLeftIcon />
							</IconButton>
						</ListItemIcon>
						<ListItemText primary={i18n.t('move column')} />
						
						<ListItemSecondaryAction>
							<IconButton size="small" onClick={() => props._moveColumn('down', column.index)}>
								<ArrowRightIcon />
							</IconButton>
						</ListItemSecondaryAction>
					</ListItem>
					<ListItem onClick={() => props._deleteColumn(column.index)} button divider dense>
						<ListItemIcon classes={{ root: classes.iconContainer }}>
							<IconButton size="small">
								<DeleteIcon />
							</IconButton>
						</ListItemIcon>
						<ListItemText primary={i18n.t('delete column')} />
					</ListItem></>}
					
					{(tab._fullscreen === undefined || (tab._fullscreen !== undefined && column.widgets.length < 1)) && <ListItem onClick={() => _openWidget({ module: config.get('DEFAULT_MODULE') })} button divider dense>
						<ListItemIcon classes={{ root: classes.iconContainer }}>
							<IconButton size="small">
								<AddIcon />
							</IconButton>
						</ListItemIcon>
						<ListItemText primary={i18n.t('add widget')} />
					</ListItem>}
					
					{tabColumnWidgetsNodes}
					
				</List>
			</Grid>
		</>
	);

});

/*
 *
 */
const Widget = React.memo(props => {
	Log._('LayoutsPage', 'RENDER Widget ' + props.widget.index, 'development');
	const classes = useStyles();
	const { widget } = props;
	
	return (
		<ListItem
			onClick={() => props._openWidget(widget)}
			button
			divider
			>
			
			{props.tab._fullscreen ?
			<ListItemIcon classes={{ root: classes.iconContainer }}>
				<FullscreenIcon />
			</ListItemIcon>
				:
			<ListItemIcon classes={{ root: classes.iconContainer }}>
				<>
					<IconButton size="small" onClick={props._moveWidget('up', widget.index)}>
						<ArrowUpIcon />
					</IconButton>
					<IconButton size="small" onClick={props._moveWidget('down', widget.index)}>
						<ArrowDownIcon />
					</IconButton>
				</>
			</ListItemIcon>}
			
			<ListItemText primary={
				<React.Fragment>
					<Typography display="block">{widget.title}</Typography>
					<Typography variant="subtitle2">({widget.module})</Typography>
				</React.Fragment>
			} />
			
		</ListItem>
	);

}, (prevProps, nextProps) => {
	//console.log('Widget', prevProps, nextProps);
	return prevProps.widget.index === nextProps.widget.index && prevProps.widget.title === nextProps.widget.title && prevProps.widget.module === nextProps.widget.module;
});
