import React from 'react'

import Button from '@material-ui/core/Button'
import Paper from '@material-ui/core/Paper'

import i18n from '../../controller/i18n'
import Log from '../../controller/Log'

import Dialog from '../../components/Dialog'
import EnhancedTable from '../../components/EnhancedTable'


/*
 * STYLES
 */
import { withStyles } from '@material-ui/core/styles'
const styles = theme => ({
	pinToTop: {
		position: 'absolute',
		top: 0,
		left: 0
	}
});


const headCells = [
	{ id: 'time', disablePadding: false, type: 'Date', label: i18n.t('Time'), noWrap: true },
	{ id: 'module', disablePadding: false, label: i18n.t('Module'), noWrap: true },
	{ id: 'criticality', disablePadding: false, label: i18n.t('Criticality'), noWrap: true },
	{ id: 'message', disablePadding: false, label: i18n.t('Message') },
	{ id: 'stack', disablePadding: false, type: 'Object', label: i18n.t('Stack Trace') },
];

const rowStyles = { // https://material-ui.com/components/alert/#simple-alerts
	'criticality': {
		'debug': {
			color: 'rgba(0, 0, 0, 0.4)',
			backgroundColor: 'rgba(0, 0, 0, 0.1)'
		},
		'info': {
			color: 'rgb(13, 60, 97)',
			backgroundColor: 'rgb(232, 244, 253)'
		},
		'warn': {
			color: 'rgb(102, 60, 0)',
			backgroundColor: 'rgb(255, 244, 229)'
		},
		'error': {
			color: 'rgb(97, 26, 21)',
			backgroundColor: 'rgb(253, 236, 234)'
		},
		'success': {
			color: 'rgb(30, 70, 32)',
			backgroundColor: 'rgb(237, 247, 237)'
			
		}
	}
}


i18n.setTranslations(language => require('./i18n/' + language));

class LogsPage extends React.Component {
	
	render() {
		const { classes } = this.props;
		
		const logs = Log.getLog();
		return (

<Dialog
	classes={{ CustomDialog: classes.pinToTop }}
	fullWidth={true}
	maxWidth={'xl'}
	
	open={(this.props.page === 'logs')}
	onClose={() => this.props.navigate('home')}
	
	header="Logs"
	actions={
		<Button autoFocus onClick={() => this.props.navigate('home')} color="primary">
			{i18n.t('Close')}
		</Button>
	}
	>
	
	<Paper className={classes.paper}>
		<EnhancedTable
			headCells={headCells}
			rows={logs}
			rowStyles={rowStyles}
			
			selectable={false}
			sortable={true}
			toolbar={false}
			stickyHeader
			/>
		
	</Paper>
	
</Dialog>

		)
	}
}

export default withStyles(styles)(LogsPage);
