import React, { useState } from 'react'

import Accordion from '@material-ui/core/Accordion'
import AccordionDetails from '@material-ui/core/AccordionDetails'
import AccordionSummary from '@material-ui/core/AccordionSummary'
import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'
import ExpandMoreIcon from '@material-ui/icons/ExpandMore'

import SettingsConfig from './Settings.config'
import i18n from '../../../controller/i18n'
import Log from '../../../controller/Log'
//import Settings from '../../../controller/Settings'

import Inputs from '../../../components/Inputs'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	heading: {
		fontSize: theme.typography.pxToRem(15),
		flexBasis: '33.33%',
		flexShrink: 0,
	},
	secondaryHeading: {
		fontSize: theme.typography.pxToRem(15),
		color: theme.palette.text.secondary,
	},
}));


i18n.setTranslations(language => require('./i18n/' + language));

const _copy = require('rfdc')();
const SettingsPage = React.memo(props => {
	Log._('SettingsPage', 'RENDER SettingsPage', 'development');
	const { _onSettingsChange } = props;
	
	/**
	 *
	 */
	let settings = _copy(props.settings);
	const _handleSettingsChange = (props, val) => {
		settings = { ...settings, [props.id]: val }
		_onSettingsChange(settings);
	}
	
	return Object.keys(SettingsConfig).map(panel => {
		return (
			<Panel
				{...props}
				key={'Panel-' + panel}
				panel={{ key: panel, ...SettingsConfig[panel] }}
				
				_handleSettingsChange={_handleSettingsChange}
				/>
		);
	});
	
}, (prevProps, nextProps) => nextProps.selected !== true); // re-render on false (not equal), only render when selected

export default SettingsPage;


/*
 *
 */
const Panel = React.memo(props => {
	Log._('SettingsPage', 'RENDER Panel', 'development');
	const { panel } = props;
	const classes = useStyles();
	
	
	const [expanded,setExpanded] = useState(false);
	
	/*
	 *
	 */
	const items = Object.keys(panel.settings).map(inputKey => {
		const inputSettings = panel.settings[inputKey];
		const Tag = Inputs[inputSettings.type || 'Text'];
		
		return (
			<Grid item key={'Input-' + panel.key + '-' + inputKey}>
				<Tag
					{...inputSettings}
					id={inputKey}
					onChange={props._handleSettingsChange}
					val={props.settings[inputKey] !== undefined ? props.settings[inputKey] : (props.settings[inputKey] !== undefined ? props.settings[inputKey] : inputSettings.val)}
					/>
				
			</Grid>
		);
	});
	
	return (
		<Accordion
			key={'Accordion-' + panel.key}
			expanded={expanded}
			onChange={(event, isExpanded) => setExpanded(isExpanded)}
			>
			
			<AccordionSummary
				expandIcon={<ExpandMoreIcon />}
				id={'panel-header-' + panel.key}
				>
				
				<Typography className={classes.heading}>{i18n.t(panel.label)}</Typography>
				{panel.description && <Typography className={classes.secondaryHeading}>{i18n.t(panel.description)}</Typography>}
				
			</AccordionSummary>
			<AccordionDetails>
				<Grid container spacing={2}>
					{items}
				</Grid>
			</AccordionDetails>
		</Accordion>
	)
});
