import React, { useState, useCallback, useMemo } from 'react'
import clsx from 'clsx'

import ButtonBase from '@material-ui/core/ButtonBase'
import IconButton from '@material-ui/core/IconButton'
import Collapse from '@material-ui/core/Collapse'
import Paper from '@material-ui/core/Paper'
import ExpandIcon from '@material-ui/icons/KeyboardArrowRight'

/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => {
	const transition = {
		duration: theme.transitions.duration.shortest,
	};
	
	return {
		expanded: {
		},
		Collapsible: {
			position: 'relative',
			transition: theme.transitions.create(['margin'], transition),
			'&:before': {
				position: 'absolute',
				left: 0,
				top: -1,
				right: 0,
				height: 1,
				content: '""',
				opacity: 1,
				backgroundColor: theme.palette.divider,
				transition: theme.transitions.create(['opacity', 'background-color'], transition),
			},
			'&:first-child': {
				'&:before': {
					display: 'none',
				},
			},
			'&$expanded': {
				margin: '16px 0',
				'&:first-child': {
					marginTop: 0,
				},
				'&:last-child': {
					marginBottom: 0,
				},
				'&:before': {
					opacity: 0,
				},
			},
			'&$expanded + &': {
				'&:before': {
					display: 'none',
				},
			}
		},
		CollapsibleSummary: {
			display: 'flex',
			minHeight: 8 * 6,
			transition: theme.transitions.create(['min-height', 'background-color'], transition),
			padding: 0,
			'&$expanded': {
				minHeight: 64,
			}
		},
		CollapsibleSummaryContent: {
			display: 'flex',
			flexGrow: 1,
			transition: theme.transitions.create(['margin'], transition),
			margin: '12px 0',
			
			'&$expanded': {
				margin: '20px 0',
			},
		},
		CollapsibleSummaryExpandIcon: {
			transform: 'rotate(0deg)',
			transition: theme.transitions.create('transform', transition),
			'&:hover': {
				// Disable the hover effect for the IconButton,
				// because a hover effect should apply to the entire Expand button and
				// not only to the IconButton.
				backgroundColor: 'transparent',
			},
			'&$expanded': {
				transform: 'rotate(90deg)',
			},
		},
		CollapsibleDetails: {
			//display: 'flex',
			padding: theme.spacing(0, 2, 2, 4),
		}
	}
});


/*
 *
 */
const CollapsibleContext = React.createContext({});

export default React.forwardRef(function Collapsible(props, ref) {
	const { expanded: defaultExpanded, children: childrenProp, className, ...other } = props;
	const classes = useStyles();
	
	const [expanded, setExpanded] = useState(defaultExpanded !== undefined ? defaultExpanded : false);
	const handleChange = useCallback(
		(event) => {
			setExpanded(!expanded);
		},
		[expanded, setExpanded]
	);
	
	const [summary, ...children] = React.Children.toArray(childrenProp);
	const contextValue = useMemo(() => ({ expanded, toggle: handleChange }), [ expanded, handleChange ]);
	
	return (
		<Paper
			elevation={0} 
			className={clsx(classes.Collapsible, { [classes.expanded]: expanded }, className)}
			ref={ref}
			{...other}
			>
			
			<CollapsibleContext.Provider value={contextValue}>{summary}</CollapsibleContext.Provider>
			<Collapse in={expanded} timeout="auto">
				<div aria-labelledby={summary.props.id} id={summary.props['aria-controls']} role="region">
					{children}
				</div>
			</Collapse>
		</Paper>
	);
});

/*
 *
 */
export const CollapsibleSummary = React.forwardRef(function CollapsibleSummary(props, ref) {
	const { children, className, expandIcon, onBlur, onClick, onFocusVisible, ...other } = props;
	const classes = useStyles();
	
	const { expanded, toggle } = React.useContext(CollapsibleContext);
	const handleChange = (event) => {
		event.stopPropagation();
		
		if (toggle) {
			toggle(event);
		}
		if (onClick) {
			onClick(event);
		}
	};
	
	return (
		<ButtonBase
			focusRipple={false}
			disableRipple
			component="div"
			aria-expanded={expanded}
			className={clsx(classes.CollapsibleSummary, { [classes.expanded]: expanded }, className)}
			onClick={handleChange}
			ref={ref}
			{...other}
			>
			
			<IconButton
				className={clsx(classes.CollapsibleSummaryExpandIcon, { [classes.expanded]: expanded })}
				component="div"
				size="small"
				tabIndex={null}
				role={null}
				aria-hidden
				>
				
				{expandIcon ? expandIcon : <ExpandIcon />}
			</IconButton>
			<div className={clsx(classes.CollapsibleSummaryContent, { [classes.expanded]: expanded })}>
				{children}
			</div>
		</ButtonBase>
	);
});

/*
 *
 */
export const CollapsibleDetails = React.forwardRef(function CollapsibleDetails(props, ref) {
	const { className, ...other } = props;
	const classes = useStyles();

	return <div className={clsx(classes.CollapsibleDetails, className)} ref={ref} {...other} />;
});
