import React, { useState, useEffect, useRef } from 'react'
//import clsx from 'clsx'
import { v1 as uuid } from 'uuid'

import Log from '../../../controller/Log'
import i18n from '../../../controller/i18n'
import Devices from '../../../controller/Devices'
import Adapters from '../../../config/adapters'

import Grid from '@material-ui/core/Grid'
import Typography from '@material-ui/core/Typography'

import Accordion from '@material-ui/core/Accordion'
import AccordionSummary from '@material-ui/core/AccordionSummary'
import AccordionDetails from '@material-ui/core/AccordionDetails'
import Chip from '@material-ui/core/Chip'
import CircularProgress from '@material-ui/core/CircularProgress'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemIcon from '@material-ui/core/ListItemIcon'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import Tooltip from '@material-ui/core/Tooltip'

import ExpandMoreIcon from '@material-ui/icons/ExpandMore'
import InfoIcon from '@material-ui/icons/Info'

import Collapsible, { CollapsibleSummary, CollapsibleDetails } from '../../../components/Collapsible'
import Dialog from '../../../components/Dialog'
import Inputs from '../../../components/Inputs'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	fullWidth: {
		width: '100%',
	},
	heading: {
		paddingRight: theme.spacing()
	},
	transparent: {
		backgroundColor: 'transparent',
	},
	ListItem: {
		display: 'flex',
	}
}));


export default function ImportDevicesPage(props) {
	const classes = useStyles();
	
	const [loading,setLoading] = useState(null);
	const [importDevices,setImportDevices] = useState({});
	const componentWillUnmount = useRef(false);
	
	let search = useRef({
		filter: 'all',
		adapters: Object.keys(Adapters) || [],
	});
	
	let foundDevicesByManufacturer = useRef({});
	let categories = useRef({
		'_new': {
			'hash': '',
			'name': i18n.t('New Devices'),
			'expanded': true,
			'devices': []
		},
		'_added': {
			'hash': '',
			'name': i18n.t('Devices that have been added already'),
			'devices': []
		},
		'_incorrect': {
			'hash': '',
			'name': i18n.t('Devices with incorrect configuration (states not found)'),
			'devices': []
		},
		'_ignored': {
			'hash': '',
			'name': i18n.t('Devices marked as ignored'),
			'devices': []
		},
	});
	
	/**
	 *
	 */
	const _onClose = () => {
		componentWillUnmount.current = true;
		props.onClose && props.onClose();
	}
	
	/**
	 *
	 */
	const _importDevices = () => props.onImport && props.onImport(importDevices);
	
	/**
	 *
	 */
	const _markDeviceToImport = (device, addOrRemove = null) => {
		
		// mark device as ignored
		let moveDeviceToIgnore = categories.current['_new'].devices.findIndex(d => d.id === device.id);
		let moveDeviceToNew = categories.current['_ignored'].devices.findIndex(d => d.id === device.id);
		
		if (device.attributes && device.attributes.ignore === true && moveDeviceToIgnore > -1) {
			categories.current['_new'].devices.splice(moveDeviceToIgnore, 1);
			categories.current['_ignored'].devices.push(device);
			categories.current['_ignored'].hash = uuid();
			categories.current['_new'].hash = uuid();
		}
		else if (device.attributes && device.attributes.ignore === false && moveDeviceToNew > -1) {
			categories.current['_ignored'].devices.splice(moveDeviceToNew, 1);
			categories.current['_new'].devices.push(device);
			categories.current['_ignored'].hash = uuid();
			categories.current['_new'].hash = uuid();
		}
		
		// mark device to import
		setImportDevices(prevImportDevices => {
			device.attributes = device.attributes || {}
			
			let deviceIds = Object.keys(prevImportDevices);
			if (addOrRemove === true || (addOrRemove === null && (deviceIds.indexOf(device.id) === -1 || device.attributes.ignore === true || device.attributes.ignore === false))) {
				device.attributes.ignore = device.attributes.ignore === false ? null : device.attributes.ignore;
				device.attributes._created = Date.now();
				
				return {
					...prevImportDevices,
					[device.id]: device
				}
			}
			
			// unmark device from import
			else {
				let nextImportDevices = { ...prevImportDevices }
				delete nextImportDevices[device.id];
				return nextImportDevices;
			}
		});
	}
	
	/**
	 *
	 */
	const _setSearch = (attribute, value) => {
		
		if (Array.isArray(search.current[attribute])) {
			
			// remove from array
			if (search.current[attribute].indexOf(value) > -1) {
				search.current[attribute] = search.current[attribute].filter(adapter => adapter !== value);
			}
			
			// add to array
			else {
				search.current[attribute].push(value);
			}
		}
		else {
			search.current[attribute] = value;
		}
	}
	
	/**
	 *
	 */
	const _setImportDeviceList = foundDevices => {
		
		// only show result list if dialogue has not been closed
		if (componentWillUnmount.current !== false) {
			return false;
		}
		
		// reset last search
		foundDevicesByManufacturer.current = {}
		categories.current._new.devices = [];
		categories.current._added.devices = [];
		categories.current._incorrect.devices = [];
		categories.current._ignored.devices = [];
		
		// add devices
		let deviceList = props.devices;
		let newDevices = {}
		
		foundDevices.forEach((res, index) => {
			let device = res.value || res.reason.device;
			device.attributes = device.attributes || {}
			
			// get category
			let categoryKey = '_new';
			if (res.status === 'fulfilled' && res.value && res.value.states && Object.keys(res.value.states).length > 0) {
				
				// device added already
				if (deviceList[device.id] !== undefined) {
					categoryKey = '_added';
					
					// device has been ignored
					if (deviceList[device.id].attributes && deviceList[device.id].attributes.ignore === true) {
						categoryKey = '_ignored';
					}
				}
				else {
					newDevices[device.id] = device;
				}
				
				// by manufacturer
				if (device.attributes.manufacturer && device.attributes.manufacturer.namespace) {
					foundDevicesByManufacturer.current[device.attributes.manufacturer.namespace] = foundDevicesByManufacturer.current[device.attributes.manufacturer.namespace] || [];
					foundDevicesByManufacturer.current[device.attributes.manufacturer.namespace].push(device.id);
				}
			}
			else {
				categoryKey = '_incorrect';
				device.states = null;
				device.attributes.note = (res.reason && res.reason.error && res.reason.error.message) || i18n.t('No correct defined states found. The device may be unknown. Please help the developer to add support for this device and report to Github.');
			}
			
			categories.current[categoryKey].devices.push(device);
		});
		
		setImportDevices(newDevices);
		setLoading('done');
	}
	
	/**
	 *
	 */
	const _getDevices = () => {
		setLoading('loading');
		
		/*
		 * retrieve devices
		 */
		if (search.current.filter === 'all') {
			Devices.importDevices(search.current.adapters)
				.then(_setImportDeviceList)
				.catch(err => {
					Log._('ImportDevicesPage', 'IMPORT_DEVICES: ' + err.message, 'error');
				});
		}
		
		/*
		 * retrieve enums
		 */
		else {
			Devices.importEnums('functions', search.current.adapters)
				.then(_setImportDeviceList)
				.catch(err => {
					Log._('ImportDevicesPage', 'IMPORT_ENUMS: ' + err.message, 'error');
				});
		}
	}
	
	/*
	 *
	 */
	const adapters = Object.keys(Adapters).map(adapterKey => {
		let adapter = { ...Adapters[adapterKey], 'namespace': adapterKey, 'devices': foundDevicesByManufacturer.current[adapterKey] || [] }
		return <AdapterItem key={'AdapterItem-' + adapterKey} adapter={adapter} tick={search.current.adapters.indexOf(adapter) > -1} _setSearch={_setSearch} />
	});
	
	return (
		<Dialog
			header={i18n.t('Devices Import')}
			actions={<>
				<Inputs.Button width="auto" id='action-cancel' onClick={_onClose} label='Cancel' variant="text" color="secondary" />
				<Inputs.Button width="auto" id='action-read' onClick={_getDevices} label='Read adapters' variant="text" disabled={loading === 'loading'} loading={loading === 'loading'} />
				{loading === 'done' && <Inputs.Button
										width="auto"
										id='action-import'
										onClick={() => _importDevices()}
										label={i18n.t('Import selected devices') + ' (' + Object.values(importDevices).filter(device => !device.attributes || device.attributes.ignore !== true).length + ')'}
										disabled={Object.keys(importDevices).length === 0}
										variant="text"
										/>}
			</>}
			
			dense
			maxWidth='md'
			fullWidth={true}
			
			open={true}
			onClose={() => props.onClose()}
			>
			
			<Inputs.Fieldset label='Adapters'>
				<Inputs.FieldsetItem>
					<Inputs.RadioGroup
						onChange={(props, val) => _setSearch('filter', val)}
						label='Search the adapter structure for all devices or only devices added to enums'
						inputs={[
							{ label: 'Search adapter structure for all devices', value: 'all' },
							{ label: 'Search only for devices added to enums', value: 'enums' }
						]}
						required
						val={search.current.filter}
						/>
					
					<List classes={{ root: classes.fullWidth }} dense>
						{adapters}
					</List>
				</Inputs.FieldsetItem>
			</Inputs.Fieldset>
			{loading !== null && <Inputs.Fieldset label='Devices'>
				<Inputs.FieldsetItem>
					{loading === 'loading' && <CircularProgress />}
					{loading === 'done' && Object.keys(categories.current).map(key => <Category key={'Category-' + key} {...{ id: key, ...categories.current[key] }} _markDeviceToImport={_markDeviceToImport} />)}
				</Inputs.FieldsetItem>
			</Inputs.Fieldset>}
		</Dialog>
	)
}


/*
 *
 */
const Category = React.memo(props => {
	const classes = useStyles();
	const { id, name, expanded, devices } = props;
	
	return (
		<Accordion classes={{ root: classes.fullWidth }} key={id} defaultExpanded={expanded === true}>
			<AccordionSummary expandIcon={<ExpandMoreIcon />}>
				<Typography className={classes.heading}>{name}</Typography>
				<Chip color="primary" size="small" label={devices.length} />
			</AccordionSummary>
			<AccordionDetails>
				<DeviceList {...props} />
			</AccordionDetails>
		</Accordion>
	)
	
}, (prevProps, nextProps) => prevProps.hash === nextProps.hash);

/*
 *
 */
const DeviceList = props => {
	const classes = useStyles();
	
	const [allSelected,setAllSelected] = useState(props.id === '_new' ? true : false);
	const deviceList = props.devices.map(device => {
		return (
			<Device
				key={'Category-' + props.id + '-Device-' + device.id}
				allSelected={allSelected}
				category={props}
				device={device}
				_markDeviceToImport={props._markDeviceToImport}
				/>
		);
	});
	
	const _onSelected = () => {
		setAllSelected(prevSelected => prevSelected === false);
	}
	
	return (
		<List classes={{ root: classes.fullWidth }} dense>
			{props.id !== '_incorrect' && props.id !== '_ignored' && <ListItem dense button divider onClick={() => _onSelected()}>
				<ListItemIcon>
					<Inputs.Checkbox
						checked={allSelected}
						tabIndex={-1}
						disableRipple
						/>
						
				</ListItemIcon>
				<ListItemText primary={<strong>{i18n.t('(Un)Select all devices')}</strong>} />
			</ListItem>}
			{deviceList}
		</List>
	);
}

/*
 *
 */
const Device = React.memo(props => {
	const classes = useStyles();
	const { category, device, _markDeviceToImport } = props;
	
	const [deviceChecked,setDeviceChecked] = useState(category.id === '_new' || (device.attributes && device.attributes.ignore !== undefined) ? true : false);
	useEffect(() => {
		setDeviceChecked(props.allSelected);
		props._markDeviceToImport(props.device, props.allSelected);
		
	}, [props]);
	
	const markDeviceToImport = () => {
		setDeviceChecked(prevChecked => prevChecked !== true);
		_markDeviceToImport && _markDeviceToImport(device);
	}
	
	return (
		<ListItem
			key={device.id}
			button={category.id !== '_incorrect' && category.id !== '_ignored'}
			dense
			divider
			onClick={() => category.id !== '_incorrect' && category.id !== '_ignored' && markDeviceToImport(device)}
			>
			
			<ListItemIcon>
				<Inputs.Checkbox
					id={device.id}
					checked={deviceChecked}
					disabled={category.id === '_incorrect' || category.id === '_ignored'}
					tabIndex={-1}
					disableRipple
					inputProps={{ 'aria-labelledby': device.id }}
					/>
					
			</ListItemIcon>
			<ListItemText classes={{ root: classes.ListItem }} disableTypography>
				<DeviceStates device={device} />
				
			</ListItemText>
			<ListItemSecondaryAction>
				{category.id === '_new' && <Inputs.Button
					width="auto"
					variant="text"
					label='ignore'
					onClick={event => {
						event.stopPropagation();
						
						device.attributes = {
							...device.attributes,
							'ignore': true
						}
						
						markDeviceToImport(device);
					}}
					/>}
					
				{category.id === '_ignored' && <Inputs.Button
					width="auto"
					variant="text"
					label='move to new devices'
					onClick={event => {
						event.stopPropagation();
						
						device.attributes = {
							...device.attributes,
							'ignore': false
						}
						
						markDeviceToImport(device);
					}}
					/>}
					
			</ListItemSecondaryAction>
		</ListItem>
	)
	
}, (prevProps, nextProps) => prevProps.device.attributes.ignore === nextProps.device.attributes.ignore && prevProps.allSelected === nextProps.allSelected);

/*
 *
 */
const DeviceStates = React.memo(props => {
	const classes = useStyles();
	const { device } = props;
	
	const states = !device.states ? <Typography display="block">{device.attributes.note}</Typography> : Object.keys(device.states).map(stateKey => <DeviceState key={'DeviceState-' + device.id + '-' + stateKey} state={{ ...device.states[stateKey], 'key': stateKey }} />);
	
	return (
		<Collapsible classes={{ root: classes.transparent }} key={'DeviceStates-' + device.id}>
			<CollapsibleSummary>
				<Typography className={classes.heading}>{device.name}</Typography>
				{device.attributes && device.attributes.manufacturer && <Chip color="secondary" size="small" label={device.attributes.manufacturer.name} />}
			</CollapsibleSummary>
			<CollapsibleDetails>
				{states}
			</CollapsibleDetails>
		</Collapsible>
	);
});

/*
 *
 */
const DeviceState = React.memo(props => {
	const { state } = props;
	
	return (
		<Grid container spacing={2}>
			<Grid item xs={12}>
				<Typography display="block"><strong>{state.key}</strong></Typography>
				
				<Typography component="span" variant="subtitle1">{i18n.t('State')}: </Typography>
				<code>{state.state || '-'}</code>
				<br />
				
				<Typography component="span" variant="subtitle1">{i18n.t('Action')}: </Typography>
				<code>{state.action || '-'}</code>
			</Grid>
		</Grid>
	);
});

/*
 *
 */
const AdapterItem = React.memo(({ adapter, tick, _setSearch }) => {
	const adapterName = adapter.default;
	
	const [checked,setChecked] = useState(tick ? tick : true);
	const setAdapter = adapterKey => {
		setChecked(prevChecked => prevChecked !== true);
		_setSearch && _setSearch('adapters', adapterKey);
	}
	
	let info = 'Damit ein Gert vom Importer erkannt wird, mssen die Datenpunkte des Gerts unter einem gemeinsamen Datenpunkt des Typs "{deviceObjectType}" strukturiert sein.';
	//info += adapter.devicePattern ? ' Auerdem muss der RegExp {devicePattern} zutreffen.' : '';
	
	return (
		<ListItem key={adapter.namespace} dense button divider onClick={() => setAdapter(adapter.namespace)}>
			<ListItemIcon>
				<Inputs.Checkbox
					id={adapter.namespace}
					checked={checked}
					tabIndex={-1}
					disableRipple
					inputProps={{ 'aria-labelledby': adapterName }}
					/>
					
			</ListItemIcon>
			<ListItemText disableTypography>
				<Typography display="inline">{adapterName + ' ' + i18n.t('adapter')}</Typography>
				<span style={{ 'marginLeft': 2, 'verticalAlign': 'bottom' }}>
					<Tooltip title={i18n.t(info).replace('{deviceObjectType}', adapter.deviceObjectType).replace('{devicePattern}', adapter.devicePattern)}>
						<InfoIcon color="primary" />
					</Tooltip>
				</span>
			</ListItemText>
			
			<ListItemSecondaryAction>
				{adapter.devices && Array.isArray(adapter.devices) && adapter.devices.length > 0 && <Chip color="primary" size="small" label={adapter.devices.length} />}
			</ListItemSecondaryAction>
		</ListItem>
	)
});