import React from 'react'
//import clsx from 'clsx'
import format from 'date-fns/format'

import Cache from '../../controller/Cache'
import i18n from '../../controller/i18n'

import Divider from '@material-ui/core/Divider'
import Grid from '@material-ui/core/Grid'
import Link from '@material-ui/core/Link'
import Paper from '@material-ui/core/Paper'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => ({
	Paper: {
		padding: theme.spacing(2)
	},
	list: {
		listStyleType: 'square'
	}
}));


i18n.setTranslations(language => require('./i18n/' + language));

export default function HelpPage(props) {
	const classes = useStyles();
	
	const jarvis = require('../../images/jarvis.png');
	const lastSent = Cache.get('lastUsageDataSent');
	return (
		<Paper classes={{ root: classes.Paper }}>
			<Grid container>
				<Grid container item xs={3} justify="center">
				<img alt="jarvis" src={jarvis} width={192} height={192} />
				</Grid>
				<Grid item xs={9}>
					<h3>{i18n.t('Help & Support')}</h3>
					<ul className={classes.list}>
						<li>jarvis Wiki: <Link target="_blank" href="https://github.com/Zefau/ioBroker.jarvis/wiki">https://github.com/Zefau/ioBroker.jarvis/wiki</Link></li>
						<li>jarvis YouTube: <Link target="_blank" href="https://www.youtube.com/channel/UCQ7rw5Uect8PSx1aVzEOlzQ/">https://www.youtube.com/channel/UCQ7rw5Uect8PSx1aVzEOlzQ/</Link></li>
						<li>ioBroker forum: <Link target="_blank" href="https://forum.iobroker.net/post/386081">https://forum.iobroker.net/post/386081</Link> und <Link target="blank" href="https://forum.iobroker.net/topic/37661/showcase-jarvis-just-another-remarkable-vis">https://forum.iobroker.net/topic/37661/showcase-jarvis-just-another-remarkable-vis</Link></li>
					</ul>
					<Divider />
					
					<h3>{i18n.t('Send Configuration Data to Developer')}</h3>
					<p>
						{i18n.t('help#sendConfigData')} <Link target="_blank" href="https://github.com/Zefau/ioBroker.jarvis/issues/36">{i18n.t('More information are documented in the Github issue')}.</Link>
					</p>
					
					<Link target="_blank" href="#tab-instances/config/system.adapter.jarvis.0">{i18n.t('You may turn off sending configuration data in the ioBroker.jarvis adapter configuration')}.</Link>
					
					<ul className={classes.list}>
						<li>{i18n.t('Help ID')}: <code>{Cache.get('identifier')}</code></li>
						<li>{i18n.t('Last Sent')}: {!lastSent ? i18n.t('never') : format(lastSent, 'dd.MM.yyyy HH:mm:ss')}</li>
					</ul>
				</Grid>
			</Grid>
		</Paper>
	);
}
