import React, { useState, useEffect } from 'react'
//import clsx from 'clsx'

import i18n from '../../../controller/i18n'
import Log from '../../../controller/Log'

import Grid from '@material-ui/core/Grid'
import List from '@material-ui/core/List'
import ListItem from '@material-ui/core/ListItem'
import ListItemText from '@material-ui/core/ListItemText'
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction'
import Typography from '@material-ui/core/Typography'

import Dialog from '../../../components/Dialog'
import Info from '../../../components/Info'
import Inputs from '../../../components/Inputs'


/*
 * STYLES
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
}));
 */


/*
 *
 */
const ComponentConfig = React.memo(props => {
	Log._('WidgetPage', 'RENDER ComponentConfig', 'development');
	//const classes = useStyles();
	const { _onClose, _onChange, id, config, attributes } = props;
	
	const [open,setOpen] = useState(false);
	const onClose = () => {
		setOpen(false)
		_onClose();
	}
	
	useEffect(() => {
		setOpen(config !== null);
	}, [config]);
	
	if (id === null || config === null || config === undefined) {
		return null;
	}
	
	// component configuration
	const componentConfiguration = Object.keys(config).map(configKey => {
		let configOption = config[configKey];
		configOption = typeof configOption === 'object' ? configOption : { 'label': configKey, 'val': configOption.val !== undefined ? configOption.val : configOption }
		let { _translate, info, ...otherConfigOption } = configOption;
		
		otherConfigOption.label = configOption.label || configKey;
		otherConfigOption.label = _translate ? i18n.t(configOption.label) : configOption.label;
		otherConfigOption.val = _translate ? i18n.t(configOption.val) : configOption.val;
		
		const Tag = Inputs[configOption.type || 'Text'];
		return (
			<ListItem key={'Config-' + configKey} divider>
				<ListItemText disableTypography>
					<Grid container spacing={1}>
						<Grid item><Info info={info} /></Grid>
						<Grid item><Typography>{i18n.t(otherConfigOption.label)}</Typography></Grid>
					</Grid>
				</ListItemText>
				
				<ListItemSecondaryAction>
					<Tag
						key={'componentConfig.' + id + '.' + configKey}
						id={'componentConfig.' + id + '.' + configKey}
						{...otherConfigOption}
						
						label=''
						width={100}
						inputProps={{
							style: { textAlign: 'right' }
						}} 
						
						onChange={(props, val) => _onChange(props, val, attributes)}
						/>
					
				</ListItemSecondaryAction>
			</ListItem>
		);
	});
	
	return (
		<Dialog
			header={i18n.t('Component configuration')}
			actions={
				<Inputs.Button key="action-save" variant="text" onClick={onClose} color="primary" label="Apply" />
			}
			
			dense
			maxWidth='xs'
			fullWidth={true}
			open={open}
			>
			
			<List dense disablePadding>
				{componentConfiguration}
			</List>
		</Dialog>
	);
});

export default ComponentConfig;
