

export default {
	"state": {
		"label": "config#Devices#state#label",
		"info": "config#Devices#state#info",
		
		//<IGNORE>
		// white space not allowed
		"onValidate": val => {
			if (val.indexOf(' ') > -1) {
				throw new Error('No whitespaces allowed in the state');
			}
			
			return val;
		}//</IGNORE>
	},
	"stateStyle": {
		"label": "config#Devices#stateStyle#label",
		"info": "config#Devices#stateStyle#info",
		"json": true
	},
	"properties": {
		"label": "config#Devices#properties#label",
		"info": "config#Devices#properties#info",
		"json": true
	},
	"display": {
		"label": "config#Devices#display#label",
		"info": "config#Devices#display#info",
		"json": true
	},
	"label": {
		"label": "config#Devices#label#label",
		"info": "config#Devices#label#info"
	},
	"unit": {
		"label": "config#Devices#unit#label",
		"info": "config#Devices#unit#info",
		"json": true,
		
		// if only a single string is given for a unit, convert it to a json compatible format to it will pass validation later
		"onValidate": val => val.indexOf(" ") === -1 && val.indexOf('"') === -1 && val.indexOf("}") === -1 && val.indexOf("{") === -1 ? '"' + val + '"' : val
	},
	"icon": {
		"label": "config#Devices#icon#label",
		"info": "config#Devices#icon#info",
		"link": "https://materialdesignicons.com/",
		"json": true,
		
		// if only a single string is given for an icon, convert it to a json compatible format to it will pass validation later
		"onValidate": val => val.indexOf(" ") === -1 && val.indexOf('"') === -1 && val.indexOf("}") === -1 && val.indexOf("{") === -1 ? '"' + val + '"' : val
	},
	"iconStyle": {
		"label": "config#Devices#iconStyle#label",
		"info": "config#Devices#iconStyle#info",
		"json": true
	},
	"placeholder_1": {
		"type": "Placeholder"
	},
	"action": {
		"label": "config#Devices#action#label",
		"info": "config#Devices#action#info",
		
		//<IGNORE>
		// white space not allowed
		"onValidate": val => {
			if (val.indexOf(' ') > -1) {
				throw new Error('No whitespaces allowed in the state');
			}
			
			return val;
		}//</IGNORE>
	},
	"bodyElement": {
		"width": 206,
		"label": "config#Devices#bodyElement#label",
		"autoComplete": true,
		"autoHighlight": true,
		"options": null,
		"renderOption": null,
		"val": "LastChangeBody"
	},
	"actionElement": {
		"width": 206,
		"label": "config#Devices#actionElement#label",
		"autoComplete": true,
		"autoHighlight": true,
		"options": null,
		"renderOption": null
	},
	"hide": {
		"label": "config#Devices#hide#label",
		"info": "config#Devices#hide#info",
		"type": "Switch"
	}
}
