import React, { useState, useRef, useEffect } from 'react'

import Paper from '@material-ui/core/Paper'
import Tabs from '@material-ui/core/Tabs'
import Tab from '@material-ui/core/Tab'
import Typography from '@material-ui/core/Typography'

import config from '../../config/config'
import Connection from '../../controller/Connection'
import Devices from '../../controller/Devices'
import i18n from '../../controller/i18n'
import Log from '../../controller/Log'
import Settings from '../../controller/Settings'

import Dialog from '../../components/Dialog'
import Inputs from '../../components/Inputs'

import ucFirst from '../../helpers/utils'
import DevicesListPage from './DevicesPage/DevicesListPage'
import HelpPage from '../HelpPage'
import LayoutsPage from './LayoutsPage/LayoutsPage'
import SettingsPage from './SettingsPage/SettingsPage'


/*
 * STYLES
 */
import { makeStyles } from '@material-ui/core/styles'
const useStyles = makeStyles(theme => ({
	paper: {
		marginBottom: '10px',
	},
	pinToTop: {
		position: 'absolute',
		top: 0,
		left: 0
	},
	title: {
		paddingLeft: theme.spacing(2),
		paddingRight: theme.spacing(1),
		marginBottom: theme.spacing(1),
		minHeight: '64px',
		display: 'flex',
		position: 'relative',
		alignItems: 'center'
	},
	rawEditor: {
		fontFamily: 'Roboto Mono,Consolas,Courier New,monospace !important'
	}
}));


i18n.setTranslations(language => require('./i18n/' + language));

const ConfigurationPage = React.memo(props => {
	Log._('ConfigurationPage', 'RENDER ConfigurationPage', 'development');
	
	// get configuration
	const devices = useRef(Devices.getDevices({ raw: true, copy: true }));
	const settings = useRef(Settings.getSettings(['iobroker']));
	const layout = useRef(Settings.getLayout());
	
	// states
	const [errors,setErrors] = useState({});
	const [expertMode,setExpertMode] = useState(settings.current.configExpertMode !== undefined ? settings.current.configExpertMode : false);
	const [tab,setTab] = useState(settings.current.configTab || 'devices');
	const [saving,setSaving] = useState(false);
	
	// set configuration
	const _onDevicesChange = d => devices.current = d;
	const _onSettingsChange = s => {
		delete s.iobroker;
		settings.current = s;
	}
	
	const _onLayoutsChange = (l = null, s = {}) => {
		if (l) {
			layout.current = l
		}
		
		settings.current = { ...settings.current, ...s }
	}
	
	/*
	 *
	 */
	const onRawChange = (props, val) => {
		
		try {
			setErrors(prevErrors => {
				let nextErrors = { ...prevErrors }
				delete nextErrors[props.id];
				return nextErrors;
			});
			
			val = JSON.parse(val);
			if (props.type === 'devices') {
				_onDevicesChange(val);
			}
			else if (props.type === 'layout') {
				_onLayoutsChange(val);
			}
			else if (props.type === 'settings') {
				_onSettingsChange(val);
			}
		}
		catch(err) {
			setErrors(prevErrors => ({
				...prevErrors,
				[props.id]: i18n.t('Invalid JSON format')
			}));
		}
	}
	
	/**
	 * 
	 */
	const _handleTabChange = (event, tab) => {
		setTab(tab);
		_onLayoutsChange(null, { 'configTab': tab });
	}
	
	/**
	 *
	 */
	const _cancel = () => props.navigate('home');
	
	/**
	 *
	 */
	const _save = (reload = true) => {
		setSaving(true);
		
		const socket = Connection.getConnection;
		const result = [
			socket.setState(config.get('NODE_SETTINGS'), JSON.stringify(settings.current)),
			socket.setState(config.get('NODE_LAYOUT'), JSON.stringify(layout.current)),
			socket.setState(config.get('NODE_DEVICES'), JSON.stringify(devices.current))
		];
		
		Promise.all(result)
			.then(() => reload ? window.location.reload() : setSaving(false))
			.catch(err => console.error(err));
	}
	
	/*
	 *
	 */
	const classes = useStyles();
	
	return (
		<Dialog
			classes={{ CustomDialog: classes.pinToTop }}
			fullWidth={true}
			maxWidth={false}
			
			open={(props.page === 'configuration')}
			onClose={_cancel}
			
			header={[
				<Typography key="header" variant="h6">
					{i18n.t('Configuration')}
					<Inputs.SwitchInline
						key="Input-expertMode"
						id="expertMode"
						size="small"
						label='Expert mode'
						onChange={(key, val) => {
							const expertTab = tab === 'help' ? tab : (val ? tab + 'Raw' : tab.substr(0, tab.length-3));
							_handleTabChange(null, expertTab);
							
							setExpertMode(val);
							_onLayoutsChange(null, { 'configExpertMode': val });
						}}
						val={expertMode}
						/>
				</Typography>,
				
				<Tabs
					key="tabs"
					value={tab}
					indicatorColor="primary"
					textColor="primary"
					onChange={_handleTabChange}
					>
					
					{expertMode === true && <Tab value="devicesRaw" label={i18n.t('Devices')} />}
					{expertMode === true && <Tab value="layoutRaw" label={i18n.t('Layout')} />}
					{expertMode === true && <Tab value="settingsRaw" label={i18n.t('Settings')} />}
					{expertMode !== true && <Tab value="devices" label={i18n.t('Devices')} />}
					{expertMode !== true && <Tab value="layout" label={i18n.t('Layout')} />}
					{expertMode !== true && <Tab value="settings" label={i18n.t('Settings')} />}
					<Tab value="help" label={i18n.t('Help')} />
				</Tabs>
			]}
			actions={[
				<Inputs.Button key='action-cancel' variant="text" onClick={_cancel} color="secondary" label="Cancel" />,
				<Inputs.Button key='action-save' width="auto" variant="text" onClick={() => _save(false)} color="primary" startIcon={null} disabled={saving === true || Object.keys(errors).length > 0} label="Save" />,
				<Inputs.Button key='action-saveapply' width="auto" variant="contained" onClick={_save} color="primary" startIcon={null} disabled={saving === true || Object.keys(errors).length > 0} label="Save & Apply" />
			]}
			>
			
			{/* DEVICES */}
			<div hidden={tab !== 'devices'} style={{ width: '100%' }}>
				{tab === 'devices' && <DevicesListPage
					selected={tab === 'devices'}
					_onLayoutsChange={_onLayoutsChange}
					_onDevicesChange={_onDevicesChange}
					layout={layout.current}
					devices={devices.current}
					/>}
				
			</div>
			<ExpertPage
				expertMode={expertMode}
				tab={tab}
				errors={errors}
				type='devices'
				onRawChange={onRawChange}
				val={devices.current}
				/>
			
			
			{/* LAYOUT */}
			<div hidden={tab !== 'layout'} style={{ width: '100%' }}>
				<Paper>
					<Typography className={classes.title} variant="h6">
						{i18n.t('Layout')}
					</Typography>
				</Paper>
				{tab === 'layout' && <LayoutsPage
					selected={tab === 'layout'}
					_onLayoutsChange={_onLayoutsChange}
					layout={layout.current}
					devices={devices.current}
					settings={settings.current}
					/>}
				
			</div>
			<ExpertPage
				expertMode={expertMode}
				tab={tab}
				errors={errors}
				type='layout'
				onRawChange={onRawChange}
				val={layout.current}
				/>
			
			
			{/* SETTINGS */}
			<div hidden={tab !== 'settings'} style={{ width: '100%' }}>
				<Paper className={classes.title}>
					<Typography variant="h6">
						{i18n.t('Settings')}
					</Typography>
				</Paper>
				{tab === 'settings' && <SettingsPage
					selected={tab === 'settings'}
					_onSettingsChange={_onSettingsChange}
					settings={settings.current}
					/>}
				
			</div>
			<ExpertPage
				expertMode={expertMode}
				tab={tab}
				errors={errors}
				type='settings'
				onRawChange={onRawChange}
				val={settings.current}
				/>
			
			
			{/* HELP */}
			<div hidden={tab !== 'help'} style={{ width: '100%' }}>
				<Paper className={classes.title}>
					<Typography variant="h6">
						{i18n.t('Help')}
					</Typography>
				</Paper>
				{tab === 'help' && <HelpPage
					selected={tab === 'help'}
					_onSettingsChange={_onSettingsChange}
					settings={settings.current}
					/>}
				
			</div>
			
		</Dialog>
	)
});

export default ConfigurationPage;


/*
 *
 */
const ExpertPage = React.memo(props => {
	const classes = useStyles();
	const { expertMode, tab, errors, type, onRawChange, val } = props;
	
	const [height,setHeight] = useState(200);
	useEffect(() => {
		const rawEditor = document.getElementById(tab) || { scrollHeight: 200 };
		setHeight(rawEditor.scrollHeight);
	}, [tab]);
	
	if (expertMode !== true) {
		return null;
	}
	
	return (
		<div hidden={tab !== (type + 'Raw')} style={{ width: '100%' }}>
			<Paper className={classes.paper}>
				<Typography className={classes.title} variant="h6">
					{i18n.t('JSON Editor') + ' ' + i18n.t(ucFirst(type))}
				</Typography>
				<Inputs.Text
					inputId={type + 'Raw'}
					label={['JSON Editor', (errors[type + 'Raw'] !== undefined ? ' (' + errors[type + 'Raw'] + ')' : '')]}
					InputProps={{ disableUnderline: true, className: classes.rawEditor }}
					inputProps={{ style: { height: height } }}
					multiline
					width="99%"
					error={errors[type + 'Raw'] !== undefined}
					onChange={(props, val) => onRawChange({ ...props, 'type': type }, val)}
					val={JSON.stringify(val || {}, null, 3)}
					/>
				
			</Paper>
		</div>
	);
});